.form-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    border: 1px solid black;
    background-image: url(https://images.pexels.com/photos/3769714/pexels-photo-3769714.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
    background-position: center;
    background-size: cover;
}

.card {
    background-color: rgba(255, 255, 255, 0.299);
    backdrop-filter: blur(19px) saturate(180%);
    box-shadow: rgba(255, 255, 255, 0.566);
    width: 30%;
}

.input {
    background-color: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(19px) saturate(180%);
    border: 1px solid #ccc;
    border-radius: 4px 4px 4px 4px;
    box-shadow: none;
}

.input:hover {
    background-color: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(19px) saturate(180%);
    border: 1px solid #ccc;
    border-radius: 4px 4px 4px 4px;
    box-shadow: none;
}

.input:focus {
    background-color: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(19px) saturate(180%);
    border: 1px solid #ccc;
    border-radius: 4px 4px 4px 4px;
    box-shadow: none;
}

.link{
    text-align: right;
    text-decoration: none;
    padding: 5px;
}

.link:hover{
    text-align: right;
    text-decoration: none;
    padding: 5px;
    color: rgb(0, 162, 255);
}

span.linktext{
    text-align: right;
    color: black;
}