.main{
    padding: 10px;
    height: 100vh;
}

.layout{
    display: flex;
}

.sidebar {
    min-height: 100%;
    width: 300px;
    border-radius: 5px;
    background-color: #007367;
    box-shadow: 0 0 2px black;
    margin-right: 20px;
    color: #f5efea;
}

.content{
    width: 100%;
    height: 100%;
}

.header{
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px black;
    background-color: #007367;
}

.body{
    height: 85vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px black;
}

.logo h6 {
    font-style: 2rem;
    margin: 20px 0px;
    text-align: center;
}

.menu-item{
    margin-top: 15px;
    margin-bottom: 15px;
}

.menu-item i{
    margin-left: 10px;
    font-size: 1.2rem;

}

.menu-item a{
    margin-left: 10px;
    text-decoration: none;
    color: #f5efea;
    font-size: 1.2rem;
}

.active{
    margin-left: 6px;
    margin-right: 15px;
    font-weight: 600;
    font-size: 1.5rem;
    background-color: #007367;
    border-radius: 8px;
    box-shadow: 5px 5px 10px rgba(0,0,0,1),
  -2px -2px 8px rgba(255, 255, 255, 0.4);
}

.header-content{
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0 15px;
}

.header-content i {
    margin-right: 20px;
    font-size: 1.2rem;
}

.header-content a{
    text-decoration: none;
    font-size: 1.2rem;
    color: #f5efea;
    margin: 0 10px;
    text-transform: uppercase;
}
